body {
  margin: 0;
  padding: 0;
  font-family: "Nunito", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fafafa;
}

p {
  font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #ebc20f;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.leaflet-container {
  height: 600px;
  width: 100%;
}

.bg-info {
  background-color: #003b46 !important;
}

.scrolled-table {
  width: 100%;
  height: 450px;
  overflow: auto;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 30px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #a7a7a7;
}

::-webkit-scrollbar-thumb:hover {
  background: #7e7e7e;
}

thead > .fixed-th > th {
  background: white;
  position: sticky;
  top: 0;
  /* Don't forget this, required for the stickiness */
  box-shadow: 0 3px 8px -7px rgba(0, 0, 0, 0.15);
  border-top: 0px solid #dee2e6;
  z-index: 6;
  vertical-align: middle;
}

.navbar-light .navbar-nav .nav-link {
  color: rgb(224 220 220 / 55%);
}

.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .show > .nav-link {
  color: rgb(255 255 255 / 90%);
}

.btn-primary {
  color: #fff;
  background-color: #66a5ad;
  border-color: #66a5ad;
}

.btn-primary:hover {
  color: #fff;
  background-color: #48838b;
  border-color: #48838b;
}

.navbar-light .navbar-brand {
  color: rgb(255, 255, 255);
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
  color: rgb(255, 255, 255);
}

.nav-link {
  color: #66a5ad;
}

.nav-link:focus,
.nav-link:hover {
  color: #48838b;
}

.table tbody tr:nth-of-type(odd) {
  background-color: #fbfbfb;
}
.table tbody tr:nth-of-type(odd):hover {
  background: #f1f1f1;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 0 solid #e9ecef;
  border-radius: 0.25rem;
  margin-bottom: 20px;

  box-shadow: 0 1px 6px 0 rgba(169, 184, 200, 0.45);
  -webkit-box-shadow: 0 1px 6px 0 rgba(169, 184, 200, 0.45);
  -moz-box-shadow: 0 1px 6px 0 rgba(169, 184, 200, 0.45);
}

.leaflet-marker-pane > img {
  cursor: pointer;
  background: white;
  padding: 2px;
  border-radius: 10px;
}

.information-icon {
  width: 40px;
  height: 40px;
  font-size: 40px;
  color: #003b46;
}

.information-content {
  width: 350px;
  position: absolute;
  background-color: white;
  right: 10px;
  z-index: 10;
  top: 45px;
  border: 1px solid #e9ecef;
  padding: 10px;
  box-shadow: 0 1px 6px 0 rgb(169 184 200 / 45%);
  border-radius: 4px;
  @media (max-width: 565px) {
    width: 330px;
  }
}

.information-container{
  padding: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.pointer {
  cursor: pointer;
}